.navigation-buttons {
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
  background-color: #f8f8f8; /* Color de fondo del pie de página */
  border-top: 1px solid #e0e0e0; /* Línea superior del pie de página */

  button {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    padding: 10px;
    font-size: 16px;
    color: gray;
    ion-icon {
      font-size: 24px;
      margin-bottom: 5px;
    }
    &.active {
      color: green; /* Cambiar a verde */
      ion-icon {
        color: green; /* Cambiar a verde */
      }
    }
  }
}
